/* --- MODAL --- */
.modal,
.modal-feedback {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  transform: translateX(120%);
  transition: transform 0.5s;
  background: #fff; }

.modal__content,
.modal-feedback__content {
  border-left: 1px solid #D9FFF5;
  padding-top: 40px;
  position: absolute;
  right: 1px;
  z-index: 2;
  max-width: 440px;
  height: 940px;
  background: #FFFFFF;
  box-shadow: 16px 0px 52px rgba(14, 24, 80, 0.2), -2px 0px 4px rgba(69, 79, 126, 0.02); }

.modal--show {
  transform: translateX(0); }

.modal__title {
  font-family: 'TT Lakes Bold';
  font-size: 24px;
  line-height: 32px;
  color: #1B1C21;
  padding-left: 32px; }

.modal__title::after {
  position: absolute;
  content: "";
  width: 4px;
  height: 40px;
  background: #41F6D7;
  border-radius: 2px;
  left: -1px; }

.modal-icon {
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  position: absolute;
  left: -70px;
  top: 59px;
  cursor: pointer; }

@media screen and (max-width: 500px) {
  .modal-icon {
    top: 5px;
    left: 7px; } }

.modal-close-icon {
  background: #FF3E79 url(./static/close.svg) no-repeat 50% 50%; }

